.action {
    &-pages {
        display: flex;
        align-items: center;
        a {
            &:first-child {
                margin-right: 26px;
            }
        }
    }
}
.quantity {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    border: 1px solid var(--mainColor);
    width: 26px;
    height: 14px;
    background-color: var(--White);
    font-size: 10px;
    font-family: var(--mainFont);
    line-height: 1.68;
    top: -8px;
    left: -20px;
}

.dropdown {
    position: relative;
    &::after {
        content: "";
        position: absolute;
        left: 0;
        top: 100%;
        height: 5px;
        width: 100%;
    }
    &__list {
        display: none;
        position: absolute;
        width: calc(100% + 10px);
        background-color: var(--White);
        left: -5px;
        top: calc(100% + 5px);
        a {
            display: flex;
            align-items: center;
            padding: 5px;
        }
    }
    &__img, img {
        width: 18px;
        height: 18px;
        border-radius: 50%;
        margin-right: 11px;
        object-fit: cover;
        flex-shrink: 0;
    }
    &__current {
        align-items: center;
    }
    &:hover {
        .dropdown__list {
            display: block;
        }
    }
}
@media(max-width: 450px) {
    .dropdown {
        &__img, img {
            margin-right: 6px;
        }
    }
}

div.social-media {
    ul {
        margin-top: 20px;
    }
    li {
        padding: 0;
        margin-right: 20px;
    }
    i {
        font-size: 15px;
    }
}

.jq-ry-container {
    padding: 0;
}